<template>
  <div id="comment">
    <div class="score">
      <div class="left">
        <h1>{{all[0] || 0}}</h1>
        <p>综合评分</p>
        <p>高于周边商家69.2%</p>
      </div>
      <div class="right">
        <p style="font-size:14px">
          服务态度
          <van-rate
            v-model="all[0]"
            allow-half
            void-icon="star"
            :size="14"
            void-color="#d5d6da"
            color="#fe9900"
            gutter="2px"
            style="vertical-align:top;margin-left:10px;margin-right:10px"
          />
          <span style="color:#fe9900">{{all[0] || 0}}</span>
        </p>
        <p style="font-size:14px">
          服务态度
          <van-rate
            v-model="all[0]"
            allow-half
            void-icon="star"
            :size="14"
            void-color="#d5d6da"
            color="#fe9900"
            gutter="2px"
            style="vertical-align:top;margin-left:10px;margin-right:10px"
          />
          <span style="color:#fe9900">{{all[0] || 0}}</span>
        </p>
        <p style="font-size:14px">
          送达时间
          <span style="margin-left:10px;color:#a1a3a8">{{all[1] || 0}}分钟</span>
        </p>
      </div>
    </div>
    <div class="middle"></div>
    <div class="allcomment">
      <div class="control">
        <div>
          <div style="background-color:#00a0dc;font-size:14px;color:#fff">
            全部
            <small style="font-size:8px">57</small>
          </div>
          <div style="background-color:#ccecf7;font-size:14px;color:#81878c">
            满意
            <small style="font-size:8px">57</small>
          </div>
          <div style="background-color:#eaebed;font-size:14px;color:#81878c">
            不满意
            <small style="font-size:8px">57</small>
          </div>
        </div>
        <div>
          <van-checkbox v-model="checked" checked-color="#b7babf" style="font-size:12px">只看有内容的评价</van-checkbox>
        </div>
      </div>
      <div class="comments" >
        <div class="allcontent" v-for="(v,i) in data" :key="i">
          <div class="icon">
            <img :src="data[i].avatar" />
          </div>
          <div class="content">
            <div>
              <span style="font-size:14px">{{data[i].username}}</span>
              <span style="font-size:14px;color:#bdbec1">{{new Date(data[i].rateTime).getFullYear()}}-{{new Date(data[i].rateTime).getMonth()+1}}-{{new Date(data[i].rateTime).getDate()}} {{new Date(data[i].rateTime).getHours()}}:{{new Date(data[i].rateTime).getMinutes()}}</span>
            </div>
            <p>
              <van-rate
              v-model="data[i].score"
              allow-half
              void-icon="star"
              :size="12"
              void-color="#d5d6da"
              color="#fe9900"
              gutter="2px"
              style="vertical-align:middle;margin-right:10px"
              />
              <span style="font-size:12px;color:#bdbec1" v-show="data[i].deliveryTime">{{data[i].deliveryTime}}分钟送达</span>
            </p>
            <p style="font-size:14px;line-height:16px">{{data[i].text}}</p>
            <p>
              <van-icon name="good-job-o" style="vertical-align:top" :class="`${data[i].rateType == 1 ? 'nogood' : 'good'}`"/>
              <span style="display:inline-block;width:40px;margin-left:10px;border:1px solid #eee;text-align:center;font-size:14px;color:#bdbec1;white-space:nowrap;overflow:hidden;text-overflow:ellipsis" v-for="(j,k) in data[i].recommend" :key="k">{{data[i].recommend[k]}}</span>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getComment } from "../api/api";
export default {
  data() {
    return {
      data: {},
      checked: true
    };
  },
  methods: {
    async comment() {
      let d = await getComment();
      this.data = d;
    }
  },
  created() {
    this.comment();
  },
  computed : {
    all(){
      let arr = [];
      let time = 0;
      let score = 0;
      let data = this.data;
      let n = 0;
      for(let i in data){
        score += data[i].score;
        if(data[i].deliveryTime){
          time += data[i].deliveryTime;
          n++;
        }
      }
      score = (score/data.length).toFixed(2);
      time = (time/n).toFixed(2);
      arr.push(parseFloat(score),parseFloat(time));
      return arr;
    }
  }
};
</script>

<style>
#comment > .score {
  box-sizing: border-box;
  display: flex;
  padding: 20px 0;
  height: 110px;
  text-align: center;
}
#comment > .score > .left {
  flex: 3;
  border-right: 1px solid #ddd;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 0 20px;
}
#comment > .score > .right {
  flex: 5;
  margin-left: 0;
  box-sizing: border-box;
  padding-left: 20px;
  text-align: left;
}
#comment > .score > .left > h1 {
  font-size: 26px;
  color: #fd9705;
}
#comment .left > p:nth-child(2) {
  font-size: 16px;
  color: #1e2a34;
}
#comment .left > p:nth-child(3) {
  font-size: 12px;
  color: #acadb1;
}
#comment .middle {
  height: 20px;
  background: #f4f5f7;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
#comment .allcomment .control {
  height: 120px;
  border-bottom: 1px solid #ddd;
  box-sizing: border-box;
  padding: 0 20px;
}
#comment .allcomment .control > div:nth-child(1) {
  display: flex;
  justify-content: flex-start;
  height: 70px;
  align-items: center;
  border-bottom: 1px solid #eee;
}
#comment .control > div:nth-child(1) > div {
  width: 65px;
  height: 34px;
  background-color: #ccc;
  margin-right: 10px;
  text-align: center;
  line-height: 34px;
}
#comment .control > div:nth-child(2) {
  height: 50px;
  display: flex;
  align-items: center;
}
#comment .comments {
  box-sizing: border-box;
  padding: 0 20px;
  margin-bottom: 60px;
}
#comment .allcontent {
  box-sizing: border-box;
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #eee;
}
#comment .allcontent > .icon {
  width: 40px;
}
#comment .allcontent > .content {
  flex: 1;
  margin-left: 15px;
}
#comment .allcontent > .icon > img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
#comment .content>div{
  display: flex;
  justify-content: space-between;
}
#comment .content>p{
  margin-bottom: 8px;
}
#comment .content .good{
  color: #01a0dc;
}
#comment .content .nogood{
  transform: rotateZ(180deg);
}
</style>
